/* reset */
body,html { height:100%; }
* { margin:0; padding:0; }
ul { list-style:none; }

/* base */
body { font-family:'Roboto'; }
a { text-decoration:none; }

/* layout */
.wrap { border:10px solid #eee; }
.header { margin-bottom:20px; border:20px solid hotpink; }
.main { border:15px dashed #fd0; padding:10px; }
.about { border:15px dashed #fd0; padding:10px; }
.contact { border:15px dashed #fd0; padding:10px; }
.footer { margin-top:20px; border:20px solid #333; }

/* header */
.gnb { border:5px dashed #eee; }
.gnb ul { display:flex; justify-content:space-between; }


/* Main */
.group { 
  display:flex; flex-wrap:wrap; 
  justify-content:space-between; 
}
.group .box { 
  width:48%;
  margin-top:10px;
  margin-bottom:10px;
  border:10px solid #0cc;
  box-sizing:border-box;
}

.group .box img { width:100%; }












